body {
  font-family: 'Muli', sans-serif;
  font-weight: 300;
  padding: 2.2em 3.6em; }

.clickable {
  cursor: pointer; }

a {
  text-decoration: none; }

.navigation {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-left: -1.3em;
  margin-right: -1.2em;
  margin-bottom: 2.5em; }
  .navigation .nav-items {
    position: absolute;
    top: 3em;
    left: 50%;
    margin-left: -6em;
    transform: scale(1, 0);
    transform-origin: top;
    transition: transform 150ms ease-in-out; }
    .navigation .nav-items .nav-item {
      text-align: center;
      list-style: none;
      margin: 0.4em auto;
      font-weight: 300;
      font-size: 2em;
      position: relative;
      opacity: 0; }
    .navigation .nav-items .nav-item.active-link::after {
      position: absolute;
      bottom: -0.1em;
      left: 0;
      content: '';
      width: 100%;
      height: 1.5px;
      border-radius: 3px;
      background: #1F6AFF;
      transform: scale(0, 1);
      transform-origin: top;
      transition: transform 300ms ease-in 200ms; }
  .navigation .github-mark {
    min-width: 35px;
    filter: invert(42%) sepia(70%) saturate(5682%) hue-rotate(213deg) brightness(100%) contrast(103%); }

.navigation.expanded {
  margin-bottom: 16em; }
  .navigation.expanded .nav-items {
    transform: scale(1, 1); }
    .navigation.expanded .nav-items .nav-item {
      opacity: 1;
      transition: opacity 150ms ease-in 250ms; }
    .navigation.expanded .nav-items .nav-item.active-link::after {
      transform: scale(1, 1); }

.flex-spacer {
  flex: 1; }

h1, h2 {
  margin: 0.9em auto;
  max-width: 520px; }

.main-title {
  color: #424242;
  font-size: 1.6em;
  line-height: 1.5em; }

.sub-title {
  color: #6D6A6A;
  font-size: 1.3em; }

.introduction {
  color: #6D6A6A;
  font-size: 1em;
  margin-bottom: 2.5em; }

.learn-more {
  color: #FD2DEE;
  white-space: nowrap; }

.primary-action-large {
  color: #FFFFFF;
  font-size: 1.8em;
  text-align: center;
  margin: auto;
  max-width: 520px;
  line-height: 1.9em;
  border-radius: 25px;
  background: #1F6AFF;
  background: linear-gradient(125deg, #1F6AFF 0%, #FD2DEE 100%);
  margin-bottom: .5em; }

.secundairy-action-large-border {
  border-radius: 25px;
  background: #1F6AFF;
  background: linear-gradient(125deg, #1F6AFF 0%, #FD2DEE 100%);
  margin: auto;
  max-width: 520px;
  padding: 2px;
  margin-bottom: 2.5em; }
  .secundairy-action-large-border .secundairy-action-large {
    color: #1F6AFF;
    font-size: 1.8em;
    text-align: center;
    line-height: 1.9em;
    border-radius: 23px;
    background: white; }

.hero-image-container {
  position: relative;
  max-width: 520px;
  margin: auto; }
  .hero-image-container .hero-image {
    width: 100%; }
  .hero-image-container .hero-image-ref {
    font-size: .8em;
    color: #7e7e7e;
    position: absolute;
    bottom: -1.5em;
    right: 0; }
